@use 'base/variables' as v;
@use 'base/mixins' as m;

.repartidores{
    padding: 5rem 0;

    &__heading{
        text-align: center;
        color: v.$secundario;
    }
    &__grid{
        @include m.tablet {
            @include m.grid(2,5rem);
        }
    }

    &__contenido{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: flex-start;
    }
    &__texto{
        line-height:1.5;
        color: v.$secundario;
        font-size: 1.7rem
    }
    &__enlace{
        @include m.boton(v.$primario,v.$blanco);
    }
}